export default "import React, { useRef } from 'react';\nimport useEmblaCarousel from 'embla-carousel-react';\nimport Autoplay from 'embla-carousel-autoplay';\nimport { useAutoplay } from './EmblaCarouselAutoplay';\nimport { useAutoplayProgress } from './EmblaCarouselAutoplayProgress';\nimport { NextButton, PrevButton, usePrevNextButtons } from '../EmblaCarouselArrowButtons';\n\nconst EmblaCarousel = (props) => {\n    const { slides, options } = props;\n    const progressNode = useRef(null);\n    const [emblaRef, emblaApi] = useEmblaCarousel(options, [\n        Autoplay({ playOnInit: false, delay: 3000 })\n    ]);\n    \n    const { prevBtnDisabled, nextBtnDisabled, onPrevButtonClick, onNextButtonClick } = usePrevNextButtons(emblaApi);\n    \n    const { autoplayIsPlaying, toggleAutoplay, onAutoplayButtonClick } = useAutoplay(emblaApi);\n    \n    const { showAutoplayProgress } = useAutoplayProgress(emblaApi, progressNode);\n    \n    return (<div className=\"embla\">\n      <div className=\"embla__viewport\" ref={emblaRef}>\n        <div className=\"embla__container\">\n          {slides.map((index) => (<div className=\"embla__slide\" key={index}>\n              <div className=\"embla__slide__number\">\n                <span>{index + 1}</span>\n              </div>\n            </div>))}\n        </div>\n      </div>\n \n      <div className=\"embla__controls\">\n        <div className=\"embla__buttons\">\n          <PrevButton onClick={() => onAutoplayButtonClick(onPrevButtonClick)} disabled={prevBtnDisabled}/>\n          <NextButton onClick={() => onAutoplayButtonClick(onNextButtonClick)} disabled={nextBtnDisabled}/>\n        </div>\n \n        <div className={`embla__progress`.concat(showAutoplayProgress ? '' : ' embla__progress--hidden')}>\n          <div className=\"embla__progress__bar\" ref={progressNode}/>\n        </div>\n \n        <button className=\"embla__play\" onClick={toggleAutoplay} type=\"button\">\n          {autoplayIsPlaying ? 'Stop' : 'Start'}\n        </button>\n      </div>\n    </div>);\n};\n\nexport default EmblaCarousel;\n";